import { Component } from "react";
import { Link } from "react-router-dom";
import ReactCodeInput from "react-code-input";
import axios from "axios";
import CreditCardInput from "react-credit-card-input";
import { authService, firebaseInstance } from "../public/firebaseConfig";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import "../style/Membership.css";
import Modal from "./Modal";
import * as config from "../config";

class Membership extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showMenu: false,
      plan: "free",
      free: "currunt",
      basic: "change",
      premium: "change",
      cardNum: "",
      buyerName: "",
      idNum: "",
      cardExpire: "",
      cardCvc: "",
      cardPwd: "",
      Price: "",
    };
    this.openModal = this.openModal.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.handleNumber = this.handleNumber.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.requestBill = this.requestBill.bind(this);
    this.changeBill = this.changeBill.bind(this);
    this.requestProfile = this.requestProfile.bind(this);
  }

  handleChange(e) {
    this.setState({ [e.target.name]: e.target.value });
  }

  handleNumber(e) {
    if (isNaN(e.target.value) === false) {
      this.setState({ [e.target.name]: e.target.value });
    }
  }

  openModal = (e) => {
    authService.onAuthStateChanged(async (user) => {
      if (authService.currentUser) {
        authService.currentUser
          .getIdToken()
          .then(async (data) => {
            await localStorage.setItem("token", data);
            console.log(e.target.name.split(" ")[0]);
            console.log(e.target.name.split(" ")[1]);
            this.setState({ plan: e.target.name.split(" ")[0] });
            this.setState({ Price: e.target.name.split(" ")[1] });
            this.setState({ showMenu: true });
          })
          .catch(async (error) => {
            toast.error(`Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.`, {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
              progress: undefined,
            });
          });
      } else {
        toast.error(`Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§.`, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    });
  };

  openModalPay = (e) => {
    this.setState({ showMenu: true });
  };

  closeModal = () => {
    this.setState({ showMenu: false });
  };

  async requestBill() {
    let user = await localStorage.getItem("token");
    if (user !== undefined) {
      const now = new Date();
      const option = {
        arsUseYn: "N",
        buyerName: this.state.buyerName, //Îì±Î°ùÏûê Ïù¥Î¶Ñ
        cardExpire:
          this.state.cardExpire.split(" / ")[1] +
          this.state.cardExpire.split(" / ")[0], //Ïú†Ìö®Í∏∞Í∞Ñ
        cardNum: this.state.cardNum.replaceAll(" ", ""), //Ïπ¥ÎìúÎ≤àÌò∏ (Ïà´Ïûê)
        cardPwd: this.state.cardPwd, //Ïπ¥Îìú ÎπÑÎ∞ÄÎ≤àÌò∏ Ïïû 2 ÏûêÎ¶¨
        idNum: this.state.idNum, //Ï£ºÎØºÎ≤àÌò∏ Ïïû 6 ÏûêÎ¶¨
        mid: "pgapppla1m", //ÏÉÅÏ†ê ÏïÑÏù¥Îîî
        moid:
          now.getFullYear() +
          "" +
          (now.getMonth() + 1) +
          now.getDate() +
          now.getHours() +
          now.getMinutes() +
          now.getSeconds(), //Í∞ÄÎßπÏ†ê Ï£ºÎ¨∏Î≤àÌò∏
        userId: (await localStorage.getItem("userUid")) + Math.random(),
      };
      console.log(option);
      axios
        .post(`https://api.innopay.co.kr/api/regAutoCardBill`, option)
        .then((response) => {
          console.log(response.data);
          if (response.data.resultCode === "0000") {
            axios
              .post(
                `${config.SERVER_URL}/pay`,
                {
                  billKey: response.data.billKey,
                  plan: this.state.plan,
                  name: this.state.buyerName,
                },
                { headers: { authentication: user } }
              )
              .then((response) => {
                console.log(response);
                this.closeModal();
              })
              .catch((error) => {
                console.log(error);
              });
          } else {
            throw new Error();
          }
          this.closeModal();
        })
        .catch((error) => {
          console.log(error);
          this.closeModal();
        });
    }
  }

  async changeBill() {
    let user = await localStorage.getItem("token");
    if (user !== undefined) {
      axios
        .put(
          `${config.SERVER_URL}/pay`,
          {
            plan: this.state.plan,
          },
          { headers: { authentication: user } }
        )
        .then((response) => {
          console.log(response);
          this.closeModal();
        })
        .catch((error) => {
          console.log(error);
        });
    }
  }

  async requestProfile() {
    let user = await localStorage.getItem("token");

    if (user !== undefined) {
      axios
        .get(`${config.SERVER_URL}/profile`, {
          headers: { authentication: user },
        })
        .then((response) => {
          localStorage.setItem("userUid", response.data.uid);
          localStorage.setItem("plan", response.data.plan);
          this.closeModal();
        })
        .catch((error) => {});
    }
  }

  async componentDidMount() {
    await this.requestProfile();
    this.setState({ free: "change" });
    this.setState({ [localStorage.getItem("plan")]: "currunt" });
  }

  render() {
    return (
      <div class="pricingDiv">
        <div class="pricing">
          <h3 class="priceTitle">free</h3>
          <div class="priceDiv">
            <span class="price1">‚Ç©</span>
            <span class="price2">0</span>
            <span class="price3">/mo</span>
          </div>
          <a class="pricebutton" onClick={this.openModal} name="free 0">
            {this.state.free}
          </a>
          <p>‚úî Ïû•Î•¥ ÏÑ†ÌÉù Î∞è Ï£ºÏù∏Í≥µ ÏûÖÎ†• Í∞ÄÎä•</p>
          <p>‚úî Ïû•ÏÜå, ÏãúÍ∞Ñ, Ï£ºÏ†ú, ÏÇ¨Í±¥ ÏûÖÎ†• Í∞ÄÎä•</p>
          <p>‚úî Ïù¥Ïñ¥Ïì∞Í∏∞ 2-3Ìöå Ï†úÍ≥µ</p>
        </div>

        <div class="pricing">
          <h3 class="priceTitle">basic</h3>
          <div class="priceDiv">
            <span class="price1">‚Ç©</span>
            <span class="price2">10000</span>
            <span class="price3">/mo</span>
          </div>
          <a class="pricebutton" onClick={this.openModal} name="basic 10000">
            {this.state.basic}
          </a>
          <p>‚úî Ïû•Î•¥ ÏÑ†ÌÉù Î∞è Ï£ºÏù∏Í≥µ ÏûÖÎ†• Í∞ÄÎä•</p>
          <p>‚úî Ïû•ÏÜå, ÏãúÍ∞Ñ, Ï£ºÏ†ú, ÏÇ¨Í±¥ ÏûÖÎ†• Í∞ÄÎä•</p>
          <p>‚úî Ïù¥Ïñ¥Ïì∞Í∏∞ Î∞è Ïù¥ÏïºÍ∏∞ ÏôÑÏÑ± Í∞ÄÎä•</p>
          <p>
            ‚úî Ïù¥ÏïºÍ∏∞ 2Í∞ú Ïù¥ÏÉÅ Ï∞ΩÏûë Í∞ÄÎä•
            <br />
            (Ïù¥ÏïºÍ∏∞ ÌïúÍ∞úÎãπ ÏµúÎåÄ Í∏∏Ïù¥ a4 2Ïû•)
          </p>
        </div>

        <div class="pricing">
          <h3 class="priceTitle">premium</h3>
          <div class="priceDiv">
            <span class="price1">‚Ç©</span>
            <span class="price2">30000</span>
            <span class="price3">/mo</span>
          </div>
          <a class="pricebutton" onClick={this.openModal} name="premium 30000">
            {this.state.premium}
          </a>
          <p>‚úî Ïû•Î•¥ ÏÑ†ÌÉù Î∞è Ï£ºÏù∏Í≥µ ÏûÖÎ†• Í∞ÄÎä•</p>
          <p>‚úî Ïû•ÏÜå, ÏãúÍ∞Ñ, Ï£ºÏ†ú, ÏÇ¨Í±¥ ÏûÖÎ†• Í∞ÄÎä•</p>
          <p>‚úî Ïù¥Ïñ¥Ïì∞Í∏∞ Î∞è Ïù¥ÏïºÍ∏∞ ÏôÑÏÑ± Í∞ÄÎä•</p>
          <p>
            ‚úî Ïù¥ÏïºÍ∏∞ 7Í∞ú Ïù¥ÏÉÅ Ï∞ΩÏûë Í∞ÄÎä•
            <br />
            (Ïù¥ÏïºÍ∏∞ ÌïúÍ∞úÎãπ ÏµúÎåÄ Í∏∏Ïù¥ a4 2Ïû•)
          </p>
        </div>

        <Modal open={this.state.showMenu} close={this.closeModal} title="Price">
          {localStorage.getItem("isBill") !== "true" ? (
            <div class="creditCard">
              <CreditCardInput
                cardNumberInputProps={{
                  value: this.state.cardNum,
                  onChange: this.handleChange,
                  name: "cardNum",
                }}
                cardExpiryInputProps={{
                  value: this.state.cardExpire,
                  onChange: this.handleChange,
                  name: "cardExpire",
                }}
                cardCVCInputProps={{
                  value: this.state.cardCvc,
                  onChange: this.handleChange,
                  name: "cardCvc",
                }}
                fieldClassName="input"
              />
              <div class="creditCardDiv">
                <span>ÎπÑÎ∞ÄÎ≤àÌò∏</span>
                <input
                  class="creditCardPwd"
                  value={this.state.cardPwd}
                  onChange={this.handleNumber}
                  name="cardPwd"
                  maxLength="2"
                ></input>
                <span>**</span>
              </div>
              <div class="creditCardDiv">
                <span>Ï£ºÎØºÎ≤àÌò∏</span>
                <input
                  class="creditCardPwd"
                  value={this.state.idNum}
                  onChange={this.handleNumber}
                  name="idNum"
                  maxLength="6"
                ></input>
                <span>-*******</span>
              </div>
              <div class="creditCardDiv">
                <span>Ïù¥Î¶Ñ</span>
                <input
                  class="creditCardPwd"
                  value={this.state.buyerName}
                  onChange={this.handleChange}
                  name="buyerName"
                  maxLength="4"
                ></input>
              </div>

              <a class="creditCardButton" onClick={this.requestBill}>
                {this.state.Price}Ïõê Í≤∞Ï†úÌïòÍ∏∞
              </a>
            </div>
          ) : (
            <div class="creditCard">
              <a class="changeButton" onClick={this.changeBill}>
                ÌîåÎûú Î∞îÍæ∏Í∏∞
              </a>
            </div>
          )}
        </Modal>

        <ToastContainer
          position="top-right"
          autoClose={3000}
          hideProgressBar={false}
          newestOnTop={false}
          closeOnClick
          rtl={false}
          pauseOnFocusLoss={false}
          draggable
          pauseOnHover
        />
      </div>
    );
  }
}

export default Membership;
